<template>
<div>
    <van-form @submit="onSubmit">
        <van-row class="myborder">
            <van-col span="24">  
                <van-field v-model="MiaoMuName"   label=""   right-icon="warning-o" placeholder=""/>
                
            </van-col>
        </van-row>
         <van-row class="myborder">
            <van-col span="24">  
                <van-field readonly clickable name="picker" :value="MiaoMuType"   label=""     placeholder="" @click="showleimuPicker = true"/>
            <van-popup v-model="showleimuPicker" position="bottom">
                    <van-picker show-toolbar :columns="LMcolumns"  @confirm="onLMConfirm"  @cancel="showleimuPicker = false" />
                </van-popup>
            </van-col>
        </van-row>
        <van-row class="myborder"> 
            <van-col span="14">  
                <van-field readonly clickable name="picker" :value="JGvalue" label="" placeholder="" @click="showPicker = true"/>
                <van-popup v-model="showPicker" position="bottom">
                    <van-picker show-toolbar :columns="JGcolumns"  @confirm="onJGConfirm"  @cancel="showPicker = false" />
                </van-popup>
            </van-col >
              <van-col span="6">
                <van-field v-model="GJgongfen"        placeholder=""/>
            </van-col>
             <van-col span="4">
                 <van-cell  value=""/>
            </van-col>
        </van-row>
         <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuGaoduL"   label=""    placeholder=""/>
            </van-col>
            <van-col span="4">  
                <van-cell  style="padding-left:5px;padding-right:5px;" value="-"/>
            </van-col>
            <van-col span="6">  
                <van-field v-model="MiaoMuGaoduH"   type="number"  placeholder=""/>
            </van-col>
            <van-col span="4">  
                <van-cell  value=""/>
            </van-col>
        </van-row>  
         <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuGuanfuL"   label="" type="number"   placeholder=""/>
            </van-col>
            <van-col span="4">  
                <van-cell  style="padding-left:5px;padding-right:5px;" value="-"/>
            </van-col>
            <van-col span="6">  
                <van-field v-model="MiaoMuGuanfuH" type="number"    placeholder=""/>
            </van-col>
            <van-col span="4">  
                <van-cell  value=""/>
            </van-col>
        </van-row>  
         <van-row class="myborder">
            <van-col span="24">  
                <van-field readonly clickable name="picker" :value="MiaoMuStatus"   label=""     placeholder="" @click="showZZZTPicker = true"/>
            <van-popup v-model="showZZZTPicker" position="bottom">
                    <van-picker show-toolbar :columns="ZZZTcolumns"  @confirm="onZZZTConfirm"  @cancel="showZZZTPicker = false" />
                </van-popup>
            </van-col>
        </van-row>
          <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuPrice"   label=""  type="number"  placeholder=""/>
            </van-col>
            <van-col span="4">  
                <van-cell  style="padding-left:5px;padding-right:5px;" v-bind:value="MiaoMuUnit"/>
            </van-col>
            <van-col span="10">  
                <van-field v-model="MiaoMuNum"   label=""   type="number" v-bind:placeholder="MiaoMuUnit"/>
            </van-col>            
        </van-row> 
    <van-row class="myborder">
        <van-col span='24'>
        <van-field v-model="MiaoMuAddress" rows="2" autosize label="" type="textarea"  placeholder="" show-word-limit/> 
    </van-col>
    </van-row>
       <van-row class="myborder">
           <van-col span="6">  
                <van-cell  style="padding-left:5px;padding-right:5px;" value=""/>
            </van-col>
             <van-col span="4">  
                 <van-cell-group>
                    <van-switch v-model="IsRemenTuijian" size="30px" style="margin: 7px 1px 7px 1px" />
                </van-cell-group>      
            </van-col>
            <van-col span="14" v-show="IsRemenTuijian">  
                <van-field readonly clickable name="picker" :value="TSvalue"  label=""  type="number"  placeholder="" @click="showTisnshuPicker = true"/>
                   <van-popup v-model="showTisnshuPicker" position="bottom">
                    <van-picker show-toolbar :columns="TScolumns"  @confirm="onTSConfirm"  @cancel="showTisnshuPicker = false" />
                </van-popup>
            </van-col>
        </van-row> 
        
       
  <van-row class="myborder" style="background-color:rgb(247, 248, 249);">
       <h5 style="margin:5px 5px"><span>      </span><span style="color:red;">9</span></h5> 
        <van-col span='24'>
       <van-uploader :after-read="afterRead" v-model="fileList"/>
    </van-col>
    </van-row>
 
    <div style="margin: 16px;">
        
        <van-button round block type="primary" native-type="submit">
        
        </van-button>
  </div>
    </van-form>
</div>
</template>
<style scoped>
.myborder{
    border-bottom: solid 1px #e8d8d8;
}
</style>
<script>
import Vue from 'vue';
import { Form } from 'vant';
import { Field } from 'vant';
import { Col, Row } from 'vant';
import { Popup } from 'vant';
import { Picker } from 'vant';
import { Switch } from 'vant';
import { Cell, CellGroup } from 'vant';
import {Button, Uploader } from 'vant';

Vue.use(Button);
Vue.use(Uploader);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Switch);
Vue.use(Picker);
Vue.use(Popup);
Vue.use(Col);
Vue.use(Row);
Vue.use(Field);
Vue.use(Form);
export default {
  data() {
    return {
        MiaoMuName:'',
        GJgongfen:'',
        MiaoMuGaoduL:'',
        MiaoMuGaoduH:'',
        JGvalue: '',
        TSvalue:'',
        MiaoMuType:'',
        MiaoMuStatus:'',
        MiaoMuNum:'',
        MiaoMuGuanfuH:'',
        MiaoMuGuanfuL:'',
        MiaoMuPrice:'',
        MiaoMuAddress:'',
        MiaoMuUnit:'/',
        IsRemenTuijian:true,
        showPicker: false,
        showTisnshuPicker:false,
        showleimuPicker:false,
        showZZZTPicker:false,
        JGcolumns: ['', '', '', '', ''],
        TScolumns: ['1', '3', '5', '7'],
        LMcolumns: ['', '', '', '','',''],
        ZZZTcolumns: ['', '', '', ''],
        fileList: [
        { url: 'https://img.yzcdn.cn/vant/leaf.jpg' },
        // Uploader 
        //  URL  isImage 
        { url: './static/mock/img/pd1.jpg', isImage: true }
      ]
    };
  },
  methods: {
    onSubmit(values) {
      console.log('submit', values);
    },
    onJGConfirm(value) {
      this.JGvalue = value;
      this.showPicker = false;
    },
    onTSConfirm(value) {
      this.TSvalue = value;
      this.showTisnshuPicker = false;
    },
    onLMConfirm(value) {
      this.MiaoMuType = value;
      this.showleimuPicker = false;
    },
    onZZZTConfirm(value) {
      this.MiaoMuStatus = value;
      this.showZZZTPicker = false;
    },
     afterRead(file) {
      // 
      console.log(file);
    }
 
  },
};
</script>


