     </van-collapse-item>
    </van-collapse>

    <van-collapse v-model="activeMore">
    <van-collapse-item title="" name="moreParam"   title-class="collapseTitle">
     <van-row class="myborder">
     <van-col span="6">
           <van-cell  value=""/>
     </van-col>
     <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueSG" :options="optionSG" />
        </van-dropdown-menu>
        </van-col>
