           <van-row class="myborder">
     <van-col span="6">
           <van-cell  value=""/>
     </van-col>
     <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueMMD" :options="optionMMD" />
        </van-dropdown-menu>
        </van-col>
       <van-col span="6">
             <van-cell  value=""/>
       </van-col>
      <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueXT" :options="optionXT" />
        </van-dropdown-menu>
