        </van-row>
        <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuGaoduL"   label=""    placeholder=""/>
            </van-col>
            <van-col span="4">  
                <van-cell   style="padding-left:5px;padding-right:5px;" value="-"/>
            </van-col>
            <van-col span="6">  
                <van-field v-model="MiaoMuGaoduH"    type="number"  placeholder=""/>
            </van-col>
            <van-col span="4">  
                <van-cell   value=""/>
            </van-col>
        </van-row>  
         <van-row class="myborder">
              <van-col span="6">  
                <van-cell   value=""/>
            </van-col>
            <van-col span="18">
            <van-radio-group v-model="MiaoMuBaoJia" direction="horizontal">
            <van-radio name="1" icon-size="24px"  style="margin: 11px 1px 11px 10px" ></van-radio>
            <van-radio name="2" icon-size="24px"  style="margin: 11px 1px 11px 10px" ></van-radio>
            </van-radio-group>
            </van-col>
          
