<style  >
.collapseTitle{
    font-size: 20px;
    font-weight: 700;
    color:#1989fa;
}
.van-collapse-item__content
{
    padding: 5px;
    color: #969799;
    font-size: 13px;
    line-height: 1.5;
    background-color: #fff;
}
.myborder{
    border-bottom: solid 1px #e8d8d8;
}
 
</style>
<script>
import Vue from 'vue';
 
import {Button,Form, Field,Col, Row ,Popup,Picker,Switch,Cell, CellGroup ,Uploader,Toast,Panel,Collapse, CollapseItem ,DropdownMenu, DropdownItem, DatetimePicker,RadioGroup, Radio } from 'vant';


Vue.use(DatetimePicker);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Collapse);
Vue.use(CollapseItem);
Vue.use(Panel);
Vue.use(Toast);
Vue.use(Uploader);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Switch);
Vue.use(Picker);
Vue.use(Popup);
Vue.use(Col);
Vue.use(Row);
Vue.use(Field);
Vue.use(Form);
Vue.use(Button);
export default {
  data() {
    return {
        testcount:1,
        MiaoMuName:'',
        GJgongfen:'',
        MiaoMuGaoduL:'',
        MiaoMuGaoduH:'',
        JGvalue: '',
        TSvalue:'',
        valueSG:0,
        valuePZ:0,
        valueTZ:0,
        valueSX:0,
        valueMMD:0,
        valueXT:0,
        MiaoMuType:'',
        MiaoMuStatus:'',
        MiaoMuBaoJia:'1',
        MiaoMuNum:'',
        MiaoMuGuanfuH:'',
        MiaoMuGuanfuL:'',
        MiaoMuPrice:'',
        MiaoMuAddress:'',
        MiaoMuUnit:'/',
        DeadDate:'',
        showDeadTimePicker:false,
        IsRemenTuijian:true,
        showPicker: false,
        showTisnshuPicker:false,
        showleimuPicker:false,
        showZZZTPicker:false,
        minDate: new Date(2020, 0, 1),
        maxDate: new Date(2026, 0, 1),
        DeadTime: '2020-03-03',
        JGcolumns: ['', '', '', '', ''],
        TScolumns: ['1', '3', '5', '7'],
        LMcolumns: ['', '', '', '','',''],
        ZZZTcolumns: ['', '', '', ''],
        activeMore: ['basicParam'],
        fileList: [],
       optionSG: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 },
        { text: '', value: 3 },
        { text: '', value: 4 }
      ],
      optionPZ: [
        { text: '', value: 0 },
        { text: 'A-', value: 1 },
        { text: 'B-', value: 2 },
         { text: 'C-', value: 3 },
          { text: 'D-', value: 4 }
      ],
       optionTZ: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 },
         { text: '', value: 3 },
          { text: '', value: 4 }
      ],
       optionSX: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 },
         { text: '', value: 3 },
          { text: '', value: 4 },
           { text: '', value: 5 },
          { text: '', value: 6 },
        { text: '', value: 7 }        
      ],
      optionMMD: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 }
      ],
       optionXT: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 },
        { text: '', value: 3 }
      ],
    };
  },
  mounted(){
console.log(this.fileList);
  },
  methods: {
    onSubmitSupportInfo(values) {
      console.log('submit', values);
    },
    onJGConfirm(value) {
      this.JGvalue = value;
      this.showPicker = false;
    },
    onTSConfirm(value) {
      this.TSvalue = value;
      this.showTisnshuPicker = false;
    },
    onLMConfirm(value) {
      this.MiaoMuType = value;
      this.showleimuPicker = false;
    },
    onZZZTConfirm(value) {
      this.MiaoMuStatus = value;
      this.showZZZTPicker = false;
    },
    onDeadTimeConfirm(tdate){
      this.showDeadTimePicker = false;
      // date.getFullYear();
      console.log(tdate);
      this.DeadDate =   this.formatDate(tdate);
     // console.log( this.formatDate(date));
     },
      formatDate(val) {
      let year = val.getFullYear()
      let month = val.getMonth() + 1
      let day = val.getDate()
      let hour = val.getHours()
      let minute = val.getMinutes()
      if (month >= 1 && month <= 9) { month = `0${month}` }
      if (day >= 1 && day <= 9) { day = `0${day}` }
      if (hour >= 0 && hour <= 9) { hour = `0${hour}` }
      if (minute >= 0 && minute <= 9) { minute = `0${minute}` }
       
      return `${year}-${month}-${day} ${hour}:${minute}`
       
    },
     afterRead(file,detail) {
      // 
     // console.log(this.fileList);
     // console.log('after'+this.testcount );
     // var obj = {url: './static/mock/img/pd'+this.testcount+'.jpg'}
     // this.fileList.push(obj);
     // console.log(this.fileList.length);

    }, 
    beforeRead(file,detail) {
        console.log(file);
        console.log(detail);
     if(file.length==undefined)
     {
        if (file.type !== 'image/jpeg') {
        Toast(' jpg ');
        return false;
      }
 
     }
     else
     {
         for(var i = 0;i<file.length;i++)
         {
        if (file[i].type !== 'image/jpeg') {
            Toast(' jpg ');
            return false;
        }
 
         }
     }
     // console.log('before'+ this.testcount);
      return true;
    },
 
  },
};
</script>


