  <van-tabbar-item replace to="/pub" icon="add">
    
  </van-tabbar-item>
   
  <van-tabbar-item replace to="/disc" icon="browsing-history">
    
  </van-tabbar-item>
  <van-tabbar-item replace to="/me" icon="manager">
    
  </van-tabbar-item>
</van-tabbar>
         
</template>
<style >
.bigfont {font-size: 25px}
</style>
<script>
import Vue from 'vue';
import { Tabbar, TabbarItem } from 'vant';
Vue.use(Tabbar);
Vue.use(TabbarItem);
export default {
 vuex: {
        getters: {
             
        },
        actions: {
            
        }
    },
    props: {},
    data() {
        return {
            active:0
        }
    },  
    methods:{
        changetab(tag){
            this.active = tag;
            console.log(tag);
        }
    },
    mounted(){
        
      this.active = this.$route.path.slice(1);
       
    } 
}
</script>


