<template>
  <div>
    <!--  -->
    <div class="top-warp">
      
        <ScrollImg :height="100" :begin="0" :interval="3000" :dot="true" :arrow="true"></ScrollImg>
      <div class="nav">
        <div :class="{'active':tabType==0}" @click="changeTab(0)">
           <label></label> 
          </div>
        <div :class="{'active':tabType==1}" @click="changeTab(1)"></div>
        <div :class="{'active':tabType==2}" @click="changeTab(2)"></div>
        <div :class="{'active':tabType==3}" @click="changeTab(3)"></div>
      </div>
    </div>
    <!---->
    <mescroll-vue ref="mescroll0" v-show="tabType==0" :down="getMescrollDown(0)" :up="getMescrollUp(0)" @init="mescrollInit0">
      <ul id="dataList0">
        <li class="data-li" v-for="pd in tab0.list" :key="pd.id">
          <img class="pd-img" v-lazy="pd.pdImg"/>
          <div class="pd-name">
            <div>
             <label> {{pd.pdName}}</label>
             <label class="pd-price" style="float:right;font-weight:800">{{pd.pdPrice}} / {{pd.pdUnit}}</label>
              </div>
         <div style="color:gray">
          <label> {{pd.pdHeight}} / {{pd.pdWeight}} </label>
           <span style="float:right">{{pd.pdLastupTime}} </span> 
           </div>
          </div>
           <div class="pd-name">
            <p class="pd-sold"> 
              <label>
                 <i class="icon icon-license" style="color:orange;font-size:15px;display:block;float:left" ></i>
                 <i class="icon icon-renzhengyonghu" style="color:green;font-size:15px;display:block;float:left" ></i>
                
                <span>{{pd.pdOwner}}</span>
                </label> 
              <label style="float:right">
                <i class="icon icon-attention" style="color:blue;font-size:15px;display:block;float:left" ></i>
                <span>{{pd.pdWatchCount}}</span>
                </label> 
              </p>
             <p class="pd-sold">
              <label> {{pd.pdAddredd}}</label>
              <label style="float:right">
                 <i class="icon icon-localization4" style="color:red;font-size:15px;display:block;float:left" ></i>
                 <span> {{pd.pdDistance}}</span>
                 </label>
               </p>
           </div>
          
         
        </li>
      </ul>
    </mescroll-vue>

    <!--  down-->
    <mescroll-vue ref="mescroll1" v-show="tabType==1" :up="getMescrollUp(1)" @init="mescrollInit1">
      <ul id="dataList1">
        <li class="data-li" v-for="pd in tab1.list" :key="pd.id">
          <img class="pd-img" :src="pd.pdImg"/>
          <div class="pd-name">{{pd.pdName}}</div>
          <p class="pd-price">{{pd.pdPrice}} </p>
          <p class="pd-sold">{{pd.pdSold}}</p>
        </li>
      </ul>
    </mescroll-vue>

    <!--  -->
    <mescroll-vue ref="mescroll2" v-show="tabType==2" :up="getMescrollUp(2)" @init="mescrollInit2">
      <ul id="dataList2">
        <li class="data-li" v-for="pd in tab2.list" :key="pd.id">
          <img class="pd-img" :src="pd.pdImg"/>
          <div class="pd-name">{{pd.pdName}}</div>
          <p class="pd-price">{{pd.pdPrice}} </p>
          <p class="pd-sold">{{pd.pdSold}}</p>
        </li>
      </ul>
    </mescroll-vue>

    <!--  -->
    <mescroll-vue ref="mescroll3" v-show="tabType==3" :up="getMescrollUp(3)" @init="mescrollInit3">
      <ul id="dataList3">
        <li class="data-li" v-for="pd in tab3.list" :key="pd.id">
          <img class="pd-img" :src="pd.pdImg"/>
          <div class="pd-name">{{pd.pdName}}</div>
          <p class="pd-price">{{pd.pdPrice}} </p>
          <p class="pd-sold">{{pd.pdSold}}</p>
        </li>
      </ul>
    </mescroll-vue>
  </div>
</template>

<script>
// mescrollvue
import MescrollVue from 'mescroll.js/mescroll.vue'
// 
import mockData from '../mock/treedata'
import ScrollImg from '../components/ScrollImg'
export default {
  name: 'mescrollMore',
  components: {
    MescrollVue,ScrollImg
  },
  data () {
    return {
      tab0: {mescroll: null, list: [], isListInit: false}, // 
      tab1: {mescroll: null, list: [], isListInit: false}, // 
      tab2: {mescroll: null, list: [], isListInit: false}, // 
      tab3: {mescroll: null, list: [], isListInit: false}, // 
      tabType: 0 // 
    }
  },
  methods: {
    // mescroll,mescroll
    mescrollInit0 (mescroll) {
      mescroll.tabType = 0; // ,list
      this.tab0.mescroll = mescroll;
    },
    mescrollInit1 (mescroll) {
      mescroll.tabType = 1;
      this.tab1.mescroll = mescroll;
    },
    mescrollInit2 (mescroll) {
      mescroll.tabType = 2;
      this.tab2.mescroll = mescroll;
    },
    mescrollInit3 (mescroll) {
      mescroll.tabType = 3;
      this.tab3.mescroll = mescroll;
    },
    // mescroll,,
    getMescrollDown (tabType) {
      let isAuto = tabType === 0; // mescrolltrue,
      return {
        auto: isAuto,
        callback: this.downCallback
      }
    },
    getMescrollUp (tabType) {
      let emptyWarpId = 'dataList' + tabType;
      return {
        auto: false,
        callback: this.upCallback, // ,;  callback: function (page) { upCallback(page); }
        noMoreSize: 4, // ,;(),; 5
        empty: {
          warpId: emptyWarpId, // id;
          icon: 'http://www.mescroll.com/img/mescroll-empty.png', // ,null
          tip: '~', // 
          btntext: ' >', // ,""
          btnClick: function () { // ,null
            alert(',')
          }
        },
        toTop: { // 
          src: 'http://www.mescroll.com/img/mescroll-totop.png' // ,null (,)
        }
      }
    },
    /*  */
    downCallback (mescroll) {
      // , tab1
      if (mescroll.tabType === 0) {
        // loadSwiper();
        
      } else if (mescroll.tabType === 1) {
        // ....
      } else if (mescroll.tabType === 2) {
        // ....
      } else if (mescroll.tabType === 3) {
        // ....
      }
      mescroll.resetUpScroll();// ,
    },
    /*  page = {num:1, size:10}; num: 1, size: */
    upCallback (page, mescroll) {
      if (mescroll.tabType === 0) {
        // 
        this.tab0.isListInit = true;// ,
        this.getListDataFromNet(mescroll.tabType, page.num, page.size, (curPageData) => {
          mescroll.endSuccess(curPageData.length);// ,;
          if (page.num === 1) this.tab0.list = []; // 
          this.tab0.list = this.tab0.list.concat(curPageData); // 
        }, () => {
          if (page.num === 1) this.tab0.isListInit = false;
          mescroll.endErr();// ,
        })
      } else if (mescroll.tabType === 1) {
        // 
        this.tab1.isListInit = true;// ,
        this.getListDataFromNet(mescroll.tabType, page.num, page.size, (curPageData) => {
          mescroll.endSuccess(curPageData.length);// ,;
          if (page.num === 1) this.tab1.list = []; // 
          this.tab1.list = this.tab1.list.concat(curPageData); // 
        }, () => {
          if (page.num === 1) this.tab1.isListInit = false;
          mescroll.endErr();// ,
        })
      } else if (mescroll.tabType === 2) {
        // 
        this.tab2.isListInit = true;// ,
        this.getListDataFromNet(mescroll.tabType, page.num, page.size, (curPageData) => {
          mescroll.endSuccess(curPageData.length);// ,;
          if (page.num === 1) this.tab2.list = []; // 
          this.tab2.list = this.tab2.list.concat(curPageData); // 
        }, () => {
          if (page.num === 1) this.tab2.isListInit = false;
          mescroll.endErr();// ,
        })
      } else if (mescroll.tabType === 3) {
        // 
        this.tab3.isListInit = true;// ,
        this.getListDataFromNet(mescroll.tabType, page.num, page.size, (curPageData) => {
          mescroll.endSuccess(curPageData.length);// ,;
          if (page.num === 1) this.tab3.list = []; // 
          this.tab3.list = this.tab3.list.concat(curPageData); // 
        }, () => {
          if (page.num === 1) this.tab3.isListInit = false;
          mescroll.endErr();// ,
        })
      }
    },

    // 
    changeTab (type) {
      let curTab = this.getTabData(this.tabType);// 
      let newTab = this.getTabData(type);// 
      curTab.mescroll.hideTopBtn(); // 
      this.tabType = type // 
      if (!newTab.isListInit) {
        newTab.mescroll.triggerDownScroll(); // 
      } else {
        setTimeout(() => {
          // 
          var curScrollTop = newTab.mescroll.getScrollTop();
          if (curScrollTop >= newTab.mescroll.optUp.toTop.offset) {
            newTab.mescroll.showTopBtn();
          } else {
            newTab.mescroll.hideTopBtn();
          }
        }, 30)
      }
    },
    // 
    getTabData (tabType) {
      if (tabType == null) tabType = this.tabType;
      if (tabType === 0) {
        return this.tab0;
      } else if (tabType === 1) {
        return this.tab1;
      } else if (tabType === 2) {
        return this.tab2;
      } else if (tabType === 3) {
        return this.tab3;
      }
    },
    /* 
          ,: http://www.mescroll.com/api.html#tagUpCallback
          getListDataFromNet,,
          ,.
          * */
    getListDataFromNet (tabType, pageNum, pageSize, successCallback, errorCallback) {
      // ,
      setTimeout(() => {
        try {
          var listData = []
          // tabType 0; 1; 2; 3;
          if (tabType === 0) {
            //  ()
            for (var i = (pageNum - 1) * pageSize; i < pageNum * pageSize; i++) {
              if (i === mockData.length) break
              listData.push(mockData[i])
            }
          } else if (tabType === 1) {
            // 
            for (var n = 0; n < mockData.length; n++) {
              if (mockData[n].pdName.indexOf('') !== -1) {
                listData.push(mockData[n])
              }
            }
          } else if (tabType === 2) {
            // 
            for (var j = 0; j < mockData.length; j++) {
              if (mockData[j].pdName.indexOf('') !== -1) {
                listData.push(mockData[j])
              }
            }
          } else if (tabType === 2) {
            // 
            for (var k = 0; k < mockData.length; k++) {
              if (mockData[k].pdName.indexOf('') !== -1) {
                listData.push(mockData[k])
              }
            }
          }
          // 
          successCallback && successCallback(listData);
        } catch (e) {
          // 
          errorCallback && errorCallback();
        }
      }, 100)
    }
  },
  beforeRouteEnter (to, from, next) { // isBounce,beforeRouteEnter
    next(vm => {
      let curMescroll = vm.$refs['mescroll' + vm.tabType]; // mescrollref,mescroll-vuebeforeRouteEnter
      curMescroll && curMescroll.beforeRouteEnter() // ,,isBounce
    })
  },
  beforeRouteLeave (to, from, next) { // isBounce,beforeRouteLeave
    let curMescroll = this.$refs['mescroll' + this.tabType]; // mescrollref,mescroll-vuebeforeRouteEnter
    curMescroll && curMescroll.beforeRouteLeave() // ,,isBounce
    next()
  } 

}
</script>

<style scoped>
  /*fixedmescroll*/
  .mescroll {
    position: fixed;
    top: 140px;
    bottom: 0;
    height: auto;
  }

  .top-warp{
    z-index: 9990;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 120px;
    background-color: white;
  }
  .top-warp .head{
    width: 100%;
    height: 44px;
    line-height: 44px;
    text-align: center;
  }
  .top-warp  .head .btn-left {
    position: absolute;
    top: 0;
    left: 0;
    padding: 12px;
    line-height: 22px;
  }
  .top-warp .tip{
    font-size: 14px;
    height: 36px;
    line-height: 36px;
    text-align: center;
  }
  .top-warp .nav{
    text-align: center;
    height: 40px;
    background: white;
  }
  .top-warp .nav > div{
    display: inline-block;
    width: 22%;
    line-height: 36px;
    font-size: 14px;
  }
  .top-warp .nav .active{
    border-bottom: 1px solid #FF6990;
    color: #FF6990;
  }

  /**/
  .data-li{
    position: relative;
    height: 90px;
    padding: 5px 2px 5px 110px;
    border-bottom: 1px solid #eee;
  }
  .data-li .pd-img{
    position: absolute;
    left: 8px;
    top: 10px;
    width: 80px;
    height: 80px;
  }
  .data-li .pd-name{
    font-size: 13px;
    line-height: 20px;
    height: 40px;
    margin-bottom: 1px;
    overflow: hidden;
  }
  .data-li .pd-price{
    font-size: 13px;
    color: red;
  }
  .data-li .pd-sold{
    font-size: 12px;
    margin-top: 1px;
    color: gray;
    margin-bottom: 1px;
  }
</style>



